@import url("https://fonts.googleapis.com/css2?family=Baloo+Da+2:wght@400;500;600;700;800&family=Muli:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap");

.header {
    font-family: "Baloo Da 2", serif;
    display: flex;
    align-items: center;
    padding: 0px 15px;
    background-color: rgb(240, 255, 240);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    text-decoration: none;
z-index: 10000000;
}


.logo {
    padding: 10px 15px;
    font-size: 30px;
    font-weight: bold;
    display: block;
    text-decoration: none;
    color: #333;

}

.link {
    margin-left: auto;
}

.ul {
    display: flex;
}


.ul li {
    list-style: none;
    margin: 0 10px;
    outline: none;
    text-decoration: none;
}

.active {
    margin-right: 10px;
    font-size: 18px;
    color: #333;
    font-weight: 500;
    padding: 20px 15px;
    display: block;
    text-decoration: none;
}    
